p, nav {
  text-align: center;
  color: azure;
  text-shadow: 0 1px 0 #111;
}

h1 {
  text-shadow: 3px 3px 3px #111;
  text-align: center;
  color: azure;
}

@media screen and (max-width: 600px) {
  .dex-top {
    min-width: 100%;
    margin: 0;
    padding: 0;
  }
}

.dex-top {
display: flexbox;
justify-self: center;
background-color: #6b0000;
padding: 1em 0;
max-width: 50%;
margin: auto;
}

.button {
box-shadow: inset 0 1px 0 0 #d8d8d8;
background: #d8d8d8 linear-gradient(to bottom, #d8d8d8 5%, #f6f6f6 100%);
border-radius: 6px;
border: 1px solid #dcdcdc;
display: inline-block;
cursor: pointer;
color: darkslategray;
font-family: Arial, sans-serif;
font-size: 15px;
font-weight: bold;
padding: 6px 24px;
text-decoration: none;
text-shadow: 0 1px 0 #ffffff;
outline: 0;
}

.activebutton {
box-shadow: inset 0 1px 0 0 #dcecfb;
background: #bddbfa linear-gradient(to bottom, #bddbfa 5%, #408bd6 100%);
border: 1px solid #84bbf3;
color: #fff;
text-shadow: 0 1px 0 #528ecc;
}